<template>
  <div class="content">
    <div v-for="(item, index) in this.newData" :key="index" class="one-list">
      <div class="title">
        <span class="head-title-span">{{ item.entity.menuName }}</span>
      </div>
      <div class="bottom-content">
        <div v-for="(item, index) in item.children" :key="index" class="more" @click="pageJump(item)">
          <!-- <span>{{item}}</span> -->
          <a href="javascript:;" v-if="item.entity.id !== 1730 || !num">
            <!-- <i
              :class="`${icon||'el-icon-document'} icon-jiahao`"
              style="color:#147BE0;fontWeight:600;"
            ></i> -->
            <span>{{ item.entity.menuName }}</span>
            <!-- <span>{{item.entity}}</span> -->
            <i class="star" :class="{
              'el-icon-star-on': item.entity.useFlag === 1 ? true : false,
              'yellow-color': item.entity.useFlag === 1 ? true : false,
              'el-icon-star-off': item.entity.useFlag === 1 ? false : true
            }" @click="setCheckStatus(item.entity, $event)">
            </i>
          </a>
          <el-badge v-else :value="num" class="item">
            <a href="javascript:;">
              <!-- <i
              :class="`${icon||'el-icon-document'} icon-jiahao`"
              style="color:#147BE0;fontWeight:600;"
            ></i> -->
              <span>{{ item.entity.menuName }}</span>
              <!-- <span>{{item.entity}}</span> -->
              <i class="star" :class="{
                'el-icon-star-on': item.entity.useFlag === 1 ? true : false,
                'yellow-color': item.entity.useFlag === 1 ? true : false,
                'el-icon-star-off': item.entity.useFlag === 1 ? false : true
              }" @click="setCheckStatus(item.entity, $event)">
              </i>
            </a>
          </el-badge>

          <!-- <MyButton style="display:inline-block" :name="item.entity.menuName" @callBackFC=""></MyButton> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { showErrorMessage, showSuccessMessage } from '@/utils/message';
import { DNC_URL, MES_URL } from '@/utils/ajaxConfigure';
import { addUserRelationshipMenu, deleteUserRelationshipMenu } from '@/api/base';
export default {
  name: 'childrenMenu',
  // 组件注册
  components: {},
  // 注册属性
  props: ['data'],
  watch: {
    data: {
      handler(val) {
        this.newData = val;
      },
      immediate: true,
      deep: true
    }
  },
  // created 在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图
  created() {},
  // mounted在模板渲染成html后调用，通常是初始化页面完成后，再对html的dom节点进行一些需要的操作
  mounted() {

  },
  // 为变量赋值等
  data() {
    return {
      newData: [],
      num: 0
    };
  },
  // 编写js函数
  methods: {
    pageJump(navMenu) {
      console.log(navMenu);
      // 把后端传过来的路径放入路由，实现路由跳转
      if (navMenu.entity.menuName === 'DNC系统') {
        const token = localStorage.getItem('token') || '';
        // const url = `${DNC_URL}?key=${token}`;
        window.open(DNC_URL, token);
      } else if (navMenu.entity.menuName === 'MES系统') {
        // eslint-disable-next-line no-redeclare
        var token = '';
        if (localStorage.getItem('mesToken') === 'undefined') {
          window.open(MES_URL);
        } else {
          // eslint-disable-next-line no-redeclare
          var tokenHeader = localStorage.getItem('mesTokenHeader');
          token = localStorage.getItem('mesToken');
          window.open(MES_URL + '?tokenHead=' + tokenHeader + '&token=' + token, token);
        }
        // const url = `${DNC_URL}?key=${token}`;
      } else if (navMenu.entity.menuName === '车间看板') {
        window.open('/kanban?cid=' + localStorage.getItem('companyId'));
      } else if (navMenu.entity.extend02 === '1') {
        const url = navMenu.entity.url.split('?')[0];
        window.open(`${url}?cid=${localStorage.getItem('companyId')}`);
        // window.open('/kanban_hx?cid=' + localStorage.getItem('companyId'));
      } else if (navMenu.entity.menuName === '安灯车间看板') {
        window.open('/kanban_andon?cid=' + localStorage.getItem('companyId'));
      } else if (navMenu.entity.menuName.indexOf('IMP') > -1 || navMenu.entity.menuName.indexOf('SCADA') > -1) {
        window.open(navMenu.entity.url);
      } else {
        console.log(navMenu);
        // this.childrenMenuDatas = [navMenu];
        this.$router.push(navMenu.entity.url);
      }
      // this.$router.push(navMenu.entity.url);
      // this.$router.push({
      //   path: navMenu.entity.url,
      //   query: { id: navMenu.entity.menuId }
      // });
    },
    /**
     * @description: 点击某个功能，切换五角星的状态
     * @param {number} id
     * @author: cyb
     */
    async setCheckStatus(itemMenu, event) {
      event.stopPropagation();
      const loading = this.$loading({
        body: true,
        lock: true,
        text: '设置提交中...'
      });
      const apiUrl = itemMenu.useFlag === 1 ? deleteUserRelationshipMenu : addUserRelationshipMenu;
      try {
        const res = await apiUrl({
          menuId: itemMenu.id
        });
        loading.close();
        if (res.code === '0000') {
          showSuccessMessage(res.message);
          itemMenu.useFlag = itemMenu.useFlag === 0 ? 1 : 0;
        } else {
          showErrorMessage(res.message);
        };
      } catch (error) {
        showErrorMessage(error);
      };
    }
  }
};
</script>

