<template>
  <div class="setCommonFunctions" v-loading="loading">
    <HeaderButtons :showRefresh="true" @buttonClickEvent="buttonClickEvent"  :cssType="1"></HeaderButtons>
    <el-row style="height: calc(100% - 40px)">
      <el-col :span="3" class="menu-list">
        <el-menu
          :collapse-transition="false"
          class="el-menu-vertical-demo"
          background-color="#eff5ff"
          text-color="#303133"
          active-text-color="#FFF"
          unique-opened
        >
          <!-- <NavMenu :navMenus="totalList"></NavMenu> -->
          <el-menu-item
            v-for="navMenu in $store.state.pageInfo.menuList"
            :index="navMenu.entity.menuName"
            @click="handleRouter(navMenu)"
            :key="navMenu.entity.id"
          >
            <div class="img-wrapper">
              <img
                class="pic-text-spacing"
                :src="require('../../assets/img/SideNavBar' + navMenu.entity.icon)"
                alt
              />
            </div>
            <span slot="title" class="position">{{ navMenu.entity.menuName }}</span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="21">
        <children-menu :data="childrenMenuDatas"></children-menu>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// import NavMenu from '@/components/common/HeadAndNav/NavMenu';
import childrenMenu from './EditMenu/childrenMenu.vue';
// 导入所需接口
// import { userGetListUserPowerAction } from '@/api/role';
import { DNC_URL, MES_URL } from '@/utils/ajaxConfigure';
export default {
  name: 'setCommonFunctionsIndex',
  components: {
    // NavMenu
    childrenMenu
  },
  data() {
    return {
      loading: false,
      totalList: [],
      childrenMenuDatas: []
    };
  },
  created() {},
  methods: {
    // 按钮触发事件
    buttonClickEvent(type, index) {
      this[`${type}`]();
    },
    async refresh() {
      this.loading = true;
      await this.$store.dispatch('pageInfo/getMenuButton');
      this.loading = false;
    },
    handleRouter(navMenu) {
      // 把后端传过来的路径放入路由，实现路由跳转
      if (navMenu.entity.menuName === 'DNC系统') {
        const token = localStorage.getItem('token') || '';
        // const url = `${DNC_URL}?key=${token}`;
        window.open(DNC_URL, token);
      } else if (navMenu.entity.menuName === 'MES系统') {
        // eslint-disable-next-line no-redeclare
        var token = '';
        if (localStorage.getItem('mesToken') === 'undefined') {
          window.open(MES_URL);
        } else {
          // eslint-disable-next-line no-redeclare
          var tokenHeader = localStorage.getItem('mesTokenHeader');
          token = localStorage.getItem('mesToken');
          window.open(MES_URL + '?tokenHead=' + tokenHeader + '&token=' + token, token);
        }
        // const url = `${DNC_URL}?key=${token}`;
      } else if (navMenu.entity.menuName === '车间看板') {
        window.open('/kanban?cid=' + localStorage.getItem('companyId'));
      } else if (navMenu.entity.menuName === '安灯车间看板') {
        window.open('/kanban_andon?cid=' + localStorage.getItem('companyId'));
      } else {
        console.log(navMenu);
        this.childrenMenuDatas = [navMenu];
        // this.$router.push(navMenu.entity.url);
      }
    },
    menuNameFunc(param) {
      if (this.GAS_NAME !== '气') {
        if (param.indexOf('气') > -1) {
          return param.replace('气', this.GAS_NAME);
        } else {
          return param;
        }
      } else {
        return param;
      }
    }
  }
};
</script>

